<template>
  <div class="mx-3">
    <v-dialog
        v-model="dialog"
        persistent
        max-width="600px"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
            color="primary"
            dark
            v-bind="attrs"
            v-on="on"
        >
          Save Template
        </v-btn>
      </template>
      <v-card>
        <v-card-title>
          <span class="text-h5">Save as template</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field
                    label="Title*"
                    v-model="title"
                    required
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field
                    label="Description*"
                    v-model="description"
                    required
                ></v-text-field>
              </v-col>
            </v-row>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
              color="blue darken-1"
              text
              @click="dialog = false"
          >
            Close
          </v-btn>
          <v-btn
              color="blue darken-1"
              text
              @click="saveTemplate"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import {axios_instance} from "@/services/ApiService";
import {mapState} from "vuex";

export default {
  computed:{
    ...mapState([
      'editorText',
    ]),
  },
  methods:{
    saveTemplate(){
      let data = new FormData();
      data.append('name', this.title);
      data.append('description', this.description);
      data.append('data', this.editorText);
      let config = {
        header : {
          "Content-Type": "multipart/form-data"
        },
      }

      axios_instance.post('/save', data, config).then(
        (response) => {
          console.log('md upload response > ', response)
        }
      )
      this.dialog = false;
    },
  },
  data: () => ({
    title: "",
    description: "",
    dialog: false,
  }),
}
</script>
