import Vue from 'vue'
import Vuex from 'vuex'
import {filters} from "@/plugins/store/modules/filter";
import {configuration} from "@/plugins/store/modules/config";
import {Product} from "@/plugins/store/modules/product";

const highlight = require('highlight.js')
const marked = require('marked')

marked.setOptions({
  highlight: function (code) {
    return highlight.highlightAuto(code).value
  }
})

Vue.use(Vuex)


const saveID = (state) => {
  let idArr = []
  for (let i = 0, len = state.articleList.length; i < len; i++) {
    idArr.push(state.articleList[i].id)
    localStorage.setItem('idArr', idArr.join(','))
  }
}

export default new Vuex.Store({
  state: {
    controller : new AbortController(),
    productsLoading : true,
    path:"",
    editorText: '',
    renderedHTML: '',
    currentQuery: '',
    templateQuery: '',
    offset: 0,
    gifs: [
      {
        id: 0,
        title: '',
        gifurl: '',
      }
    ],
    templates: [
      {
        Id: 0,
        Filename: '',
        Description: '',
      }
    ]
  },
  mutations: {
    removeElements() {
      this.$store.commit('cpu/setCurrent', null)
      this.$store.commit('gpu/setCurrent', null)
      this.$store.commit('fan/setCurrent', null)
      this.$store.commit('ram/setCurrent', null)
      this.$store.commit('chassis/setCurrent', null)
      this.$store.commit('psu/setCurrent', null)
      this.$store.commit('motherboard/setCurrent', null)
    },
    setController(state) {
      state.controller.abort();
      state.controller = new AbortController();
    },
    setProductsLoading(state, payload) {
      state.productsLoading = payload;
    },
    currentPath(state, path) {
      state.path = path;
    },
    updateEditorText(state, text) {
      state.editorText = text;
    },
    updateOffsetText(state, off) {
      state.offset = off;
    },
    searchText(state, text) {
      state.currentQuery = text;
    },
    pushResult(state, g){
      state.gifs = g;
    },
    pushTemps(state, t){
      state.templates = t;
    },
    /*input_text(state, txt){
      for (let i = 0, len = state.article.length; i < len; i++) {
        if (state.article[i].current) {
          state.article[i].content = txt
        }
      }
    },*/
    SAVE_TO_CACHE (state) {
      for (let i = 0, len = state.article.length; i < len; i++) {
        if (state.article[i].current) {
          localStorage.setItem(state.article[i].id, state.articleList[i].content)
          saveID(state)
        }
      }
    },
  },
  actions: {
    inputText ({ commit }, txt){
      commit("updateEditorText", txt)
    },
    updateOffset({ commit }, off){
      console.log(off)
      commit("updateOffsetText", off)
    },
    saveToCache ({ commit }) {
      commit('SAVE_TO_CACHE')
    },
    inputSearch ({ commit }, txt){
      commit("searchText", txt)
    },
    pushGifs ({ commit }, gifs){
      commit("pushResult", gifs)
    },
    pushTemplates ({ commit }, templates){
      commit("pushTemps", templates)
    },
  },
  getters: {
    getController : state => {
      return state.controller;
    },
    getProductsLoading : state => {
      return state.productsLoading;
    },
    getPath : state => {
      return state.path;
    },
  },
  modules: {
    configurator : configuration,
    filters : filters,
    cpu : new Product(),
    gpu : new Product(),
    chassis : new Product(),
    psu : new Product(),
    ram : new Product(),
    motherboard : new Product(),
    fan : new Product()
  },
})
