<template>
  <div class="search-gif">
    <v-row>
      <v-text-field
          label="Search"
          clearable
          v-model="toSearch"
      ></v-text-field>
      <v-btn
          @click="startSearching" icon>
        <v-icon>mdi-magnify</v-icon>
      </v-btn>
    </v-row>
  </div>
</template>

<script>
import ApiService from "../services/ApiService";

export default {
  name: 'SearchField',
  data () {
    return {
    }
  },
  computed:{
    toSearch:{
      get() {
        return this.currentQuery;
      },
      set(val) {
        //  this.updateEditorText(value);
        console.log(val );
        this.$store.dispatch('inputSearch', val)
      },
    }
  },
  methods:{
    startSearching(){
      let query = this.$store.state.currentQuery;
      ApiService.searchGiphy(query).
      then(result => {
        this.gifs = [];
        result.data.data.forEach(element => {
          this.gifs.push({ id: element.id, title: element.title, gifurl: element.images.fixed_width.url})
        })
        this.$store.dispatch('pushGifs', this.gifs);
      }, error => {
        console.error(error);
      });
    }
  }
}
</script>

<style scoped>
.search-gif{
}

</style>
