<template>
  <div class="mx-3">
    <v-dialog
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
            color="primary"
            dark
            v-bind="attrs"
            v-on="on"
        >
          Template
        </v-btn>
      </template>
      <v-card>
        <v-toolbar
            dark
            color="primary"
        >
          <v-btn
              icon
              dark
              @click="dialog = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Settings</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-toolbar-items>
            <v-btn
                dark
                text
                @click="chooseItem"
            >
              Save
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-list
            three-line
            subheader
        ></v-list>
        <v-main>
          <v-card>
            <v-list three-line>
              <v-list-item-group
                  v-model="selectedItem"
                  active-class="blue--text"
              >
                <v-list-item
                    v-for="t in getTemplates"
                    :key="t.id"
                    @click="selectedItem = t.id"
                >
                  <v-list-item-content>
                    <v-list-item-title v-html="t.Filename" ></v-list-item-title>
                    <v-list-item-subtitle v-html="t.Description"></v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
                <v-divider></v-divider>
              </v-list-item-group>
            </v-list>
          </v-card>
        </v-main>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import markdown from "@/api/markdown";
import ApiService from "@/services/ApiService";

export default {
  created() {
    ApiService.getAllTemplates().then(result => {
      this.ts = [];
      result.data.forEach(element => {
        this.ts.push(element)
      })
      this.$store.dispatch('pushTemplates', this.ts);
    }, error => {
      console.error(error);
    });
  },
  computed: {
    /*...mapState([
      'templates.data',
    ]),*/
    getTemplates(){
      // eslint-disable-next-line vue/no-async-in-computed-properties
      return this.$store.state.templates
    },
  compiledMarkdown() {
    return markdown.markdownToHTML(this.$store.state.templates.data);
  },
  },
  methods:{
    startSearching(){
      let query = this.$store.state.templateQuery;
      ApiService.searchTemplate(query).
      then(result => {
        this.templates = [];
        result.data.forEach(element => {
          this.templates.push(element)
        })
        this.$store.dispatch('pushTemplates', this.templates);
      }, error => {
        console.error(error);
      });
    },
    chooseItem(){
      this.dialog = false;
      console.log(this.selectedItem)
      ApiService.getTemplateById(this.selectedItem).
      then(result => {
        this.selectedTemplate = result.data;
      //  result.data.forEach(element => {
       //   this.selectedTemplate = element;
      //  })
        /*this.$store.dispatch('pushCurrentTemplate', this.selectedTemplate);*/
        this.$store.dispatch('inputText', this.selectedTemplate);
        this.$store.dispatch('updateOffset', this.selectedTemplate.length)
      }, error => {
        console.error(error);
      });
    },
    updateTemplates(){
      ApiService.getAllTemplates().then(result => {
        this.ts = [];
        result.data.forEach(element => {
          this.ts.push(element)
        })
        this.$store.dispatch('pushTemplates', this.ts);
      }, error => {
        console.error(error);
      });
    },
  },
  data () {
    return {
      ts: [],
      selectedItem: 0,
      selectedTemplate: "",
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
    }
  },
}
</script>
