import axios from "axios"

export const axios_instance = axios.create({
  //  baseURL : "http://localhost:80/products",
    baseURL :  process.env.VUE_APP_BACKEND_URL + "/products",
});

function fetchGiphy (endpoint) {
    return instance.get(endpoint)
}

function fetchTemplate (endpoint) {
    return axios_instance.get(endpoint)
}

export const instance = axios.create({
    baseURL: 'https://api.giphy.com/v1/',
})

export default {
    searchGiphy (query, limit = 12) {
        return fetchGiphy(`/gifs/search?&api_key=${process.env.VUE_APP_GIPHY_KEY}&q=${query}&limit=${limit}&offset=0&rating=g&lang=en`)
     },

    searchTemplate (query) {
        return fetchTemplate(`/${query}`)
    },
   /* getGifById (gifId) {
        return fetch(`/gifs/${gifId}?api_key=${ENV.giphy.apiKey}`)
    }*/
    getAllTemplates() {
        return fetchTemplate(`/all`)
    },
    getTemplateById(selectedItem) {
        return fetchTemplate(`/${selectedItem}`)
    }
}
