import Vue from 'vue'
import VueRouter from 'vue-router'
import AuthPage from "@/pages/AuthPage";

Vue.use(VueRouter)
const routes = [

  {
    name: 'configurator',
    path: '/config',
    component: AuthPage
  },
  {
    name: 'main',
    path: '/',
    component: AuthPage
  },
  {
    name: 'auth',
    path:'/authentication',
    component: AuthPage
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
})


export default router
