<template>
  <div class="mx-3">
    <v-dialog
        v-model="dialog"
        fullscreen
        hide-overlay
        transition="dialog-bottom-transition"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
            color="primary"
            dark
            v-bind="attrs"
            v-on="on"
        >
          {{ title }}
        </v-btn>
      </template>
      <v-card>
        <v-toolbar
            dark
            color="primary"
        >
          <v-btn
              icon
              dark
              @click="dialog = false"
          >
            <v-icon>mdi-close</v-icon>
          </v-btn>
          <v-toolbar-title>Settings</v-toolbar-title>
          <v-spacer></v-spacer>
          <div class="mt-5 mr-3">
            <SearchField/>
          </div>
          <v-toolbar-items>
            <v-btn
                dark
                text
                @click="insertGif"
            >
              Add
            </v-btn>
          </v-toolbar-items>
        </v-toolbar>
        <v-list
            three-line
            subheader
        ></v-list>
        <v-main>
          <v-container>
            <v-row>
              <v-col
                  v-for="gif in getGifs"
                  v-bind:key="gif.id"
                  v-on:click="gf = gif"
                  cols="4"
              >
                <v-card height="200" v-bind:img="gif.gifurl"></v-card>
              </v-col>
            </v-row>
          </v-container>
        </v-main>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import SearchField from "@/components/SearchField";
export default {
  components: {SearchField},
  computed:{
    getGifs(){
      console.log(this.$store.state.gifs)
      return this.$store.state.gifs;
    },
    getGif(){
      return this.$store.state.gifs.original.url;
    },
  },
  methods: {
    updateView (){
      console.log(this.$store.state.gifs)
    },
    insertGif (){
      console.log(this.gf)
      this.$store.dispatch('inputText',  this.$store.state.editorText + "![Alt Text](" + this.gf.gifurl + ")");
      this.dialog = false;
    }
  },
  props:{
    title: String
  },
  data () {
    return {
      dialog: false,
      notifications: false,
      sound: true,
      widgets: false,
      gf: null,
    }
  },
}
</script>
